<template>
  <div class="eatmedicinetimelist">
    <v-dialog v-model="dialog" width="70%">
      <template v-slot:activator="{ on }">
        <v-icon color="brown" dark large v-on="on" @click="enterProcess">mdi-cog-box</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <!-- <pre>{{list}}</pre> -->
          <v-form ref="form" class="mt-3">
            <v-row>
              <v-col cols="12" md="4">
                <v-text-field
                  prepend-icon="mdi-timetable"
                  label="用藥時間名稱 or 搜尋"
                  v-model="list.name"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-text-field prepend-icon="mdi-av-timer" label="時間設定(請用,隔開)" v-model="list.time"></v-text-field>
              </v-col>
              <v-col cols="12" md="2">
                <v-text-field prepend-icon="mdi-calendar-range" label="間隔天數" v-model="list.day"></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-btn class="primary" block @click="addOK" v-show="!editMode">確認新增</v-btn>
                <v-btn class="success" block @click="editOK" v-show="editMode">確認修改</v-btn>
                <v-btn class="error mt-1" block @click="cancelEditMode" v-show="editMode">取消修改</v-btn>
              </v-col>
            </v-row>
          </v-form>

          <v-card class="my-2">
            <v-card-text>
              <v-sheet color="green lighten-3" outlined class="text-center pa-2">
                <h3 class="white--text">已建構用藥時間列表</h3>
              </v-sheet>

              <v-data-iterator
                :items="filterItems"
                :items-per-page.sync="itemsPerPage"
                :footer-props="{ itemsPerPageOptions }"
              >
                <template v-slot:default="props">
                  <v-list>
                    <v-list-item-group>
                      <v-list-item v-for="(item, i) in props.items" :key="i">
                        <v-list-item-content @click.stop="enterEditMode(item)">
                          <v-list-item-title v-text="item.text"></v-list-item-title>
                        </v-list-item-content>
                        <v-icon color="red" @click.stop="del(item)">mdi-close</v-icon>
                      </v-list-item>
                    </v-list-item-group>
                  </v-list>
                </template>
              </v-data-iterator>
            </v-card-text>
          </v-card>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <!-- <v-btn class="primary" @click="addOK" v-if="processType=='add'">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="processType=='edit'">確認修改</v-btn>-->
        </v-card-actions>
        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      items:[],
      dialog: false,
      list: {
        name: "",
        day: 1,
      }, //表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
      itemsPerPage: 10,
      itemsPerPageOptions: [10, 20, 30],
      editMode: false,
    };
  },

  mounted(){
    this.getAllData();
  },

  computed: {
    filterItems() {
      if (this.items.length > 0){
        var str = "";
        return this.items.filter(item => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(this.list.name.toUpperCase())) {
            return item;
          }
        });
      }
    }
  },

  methods: {
    getAllData() {
      //取得所有病歷資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      this.axios
        .post("general/getAll/" + this.$store.state.databaseName + "/eat_medicine_time")
        .then(rs => {
          var newData = rs.data.map(item=>{
            return {
              snkey: item.snkey,
              text: item.name + " [" + item.time + "]" + " / 間隔 " + item.day + "天",
              time: item.time,
              day: item.day,
            }
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
          this.items = newData.sort(function(a,b){
            return parseInt(a.snkey) < parseInt(b.snkey) ? 1 : -1;
          })
        });
    },
    enterProcess() {
      //進入畫面時,執行的是新增作業
      this.title = "用藥時間資料管理";
      this.titleStyle = "font-weight-black primary lighten-2";
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        //判斷重覆

        var repeat = this.items.find(item => {
          return (this.list.name == item.name)
        });

        if (repeat != undefined) {
          var pop = { msg: "不可重覆!!", type: true, theme: "warning" };
          this.$store.commit("snackbar", pop);
          return;
        }

        this.$store.commit("getTime"); //取得現在時間
        this.list["create_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/eat_medicine_time",
            qs.stringify(this.list)
          )
          .then(rs => {
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.$emit("")
              // this.$store.dispatch("getItems", "hospital"); //重新讀入資料
              this.getAllData();
              this.list.name = "";
              this.$refs.form.resetValidation();
              this.$emit("getSelect");
            }
          });
      }
    },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then(res => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey:delData.snkey,
          tablename:'eat_medicine_time',
          info: JSON.stringify(delData),
        };

        if (res) {
          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/eat_medicine_time",
              qs.stringify(postData)
            )
            .then(rs => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
                this.$emit("getSelect");
                // this.$store.dispatch("getItems", "hospital"); //重新讀入資料
              }
            });
        }
      });
    },
    enterEditMode(item) {
      //進入修改模式
      this.list.snkey = item.snkey;
      this.list.name = item.text;
      this.list.edit_man = '';
      this.editMode = true;
    },
    editOK() {
      //確認修改
      this.axios
        .post(
          "general/edit/" + this.$store.state.databaseName + "/eat_medicine_time",
          qs.stringify(this.list)
        )
        .then(rs => {
          // console.log(rs.data)
          if (rs.data.state == 1) {
            var pop = { msg: "已修改", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.getAllData();
            this.$emit("getSelect");
            // this.$store.dispatch("getItems", "hospital"); //重新讀入資料
            this.list = {
              name: ""
            };
            this.$refs.form.resetValidation();
            this.editMode = false;
          }
        });
    },
    cancelEditMode() {
      //取消修改
      this.editMode = false;
      this.list = {
        name: ""
      };
      this.$refs.form.resetValidation();
    }
  },
};
</script>

<style lang="scss">
