<template>
  <div class="MedicalRecordAdd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>
          {{ title }}
          <v-spacer></v-spacer>
          <v-btn class="primary" @click.stop="insertLast"
            >載入最近一次就診資料</v-btn
          >
        </v-card-title>

        <v-card-text>
          <v-alert text color="primary" class="mt-2" outlined>
            住民資料
            <v-row>
              <v-col cols="12" md="2">
                <v-text-field
                  label="床位"
                  outlined
                  readonly
                  hide-details
                  :value="this.$store.state.uData.bed_name"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="2">
                <v-text-field
                  label="姓名"
                  outlined
                  readonly
                  hide-details
                  :value="this.$store.state.uData.name"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="2">
                <v-text-field
                  label="住民證號"
                  outlined
                  readonly
                  hide-details
                  :value="this.$store.state.uData.p_code"
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-text-field
                  label="疾病史"
                  outlined
                  readonly
                  hide-details
                  :value="diagnosis"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-alert>
          <v-form ref="form" class="mt-3">
            <v-row>
              <v-col cols="12" md="2">
                <v-text-field
                  label="就診日期"
                  type="date"
                  v-model="list.date"
                  :rules="emptyRules"
                ></v-text-field>
                <!-- <datepicker :label="'就診日期'" v-model="list.date" :rules="emptyRules"></datepicker> -->
              </v-col>
              <v-col cols="12" md="2">
                <v-select
                  :items="outpatientItems"
                  label="診別"
                  v-model="list.outpatient"
                ></v-select>
              </v-col>
              <v-col cols="12" md="2">
                <v-select
                  :items="timeItems"
                  label="時段"
                  v-model="list.time"
                ></v-select>
              </v-col>
              <v-col cols="12" md="3" class="d-flex align-center">
                <v-autocomplete
                  label="醫院"
                  :items="hospitalItems"
                  v-model="list.hospitaldata"
                  :rules="emptyRules"
                  return-object
                ></v-autocomplete>
                <!-- <Selecter
                  :label="'醫院'"
                  :dataSource="'hospital'"
                  :defaultData="list.hospital_name"
                  @returnFn="getHospital"
                ></Selecter>-->
                <HospitalList
                  @getSelect="getSelect"
                  v-show="this.$store.state.pData.option_set_key"
                ></HospitalList>
              </v-col>
              <v-col cols="12" md="3" class="d-flex align-center">
                <v-autocomplete
                  label="科別"
                  :items="branchItems"
                  v-model="list.branchdata"
                  :rules="emptyRules"
                  return-object
                ></v-autocomplete>
                <!-- <Selecter
                  :label="'科別'"
                  :dataSource="'branch'"
                  :defaultData="list.branch_name"
                  @returnFn="getBranch"
                ></Selecter>-->
                <BranchList
                  @getSelect="getSelect"
                  v-show="this.$store.state.pData.option_set_key"
                ></BranchList>
              </v-col>
              <v-col cols="12" md="2" class="d-flex align-center">
                <v-autocomplete
                  label="醫生"
                  :items="doctorItems"
                  v-model="list.doctordata"
                  :rules="emptyRules"
                  return-object
                ></v-autocomplete>
                <!-- <Selecter
                  :label="'醫生'"
                  :dataSource="'doctor'"
                  :defaultData="list.doctor_name"
                  @returnFn="getDoctor"
                ></Selecter>-->
                <DoctorList
                  @getSelect="getSelect"
                  v-show="this.$store.state.pData.option_set_key"
                ></DoctorList>
              </v-col>
              <v-col cols="12" md="7" class="d-flex align-center">
                <v-autocomplete
                  label="就診主因"
                  :items="main_cause_of_treatmentItems"
                  v-model="list.main_cause_of_treatmentdata"
                  :rules="emptyRules"
                  return-object
                ></v-autocomplete>
                <!-- <Selecter
                  :label="'就診主因'"
                  :dataSource="'main_cause_of_treatment'"
                  :defaultData="list.main_cause_of_treatment_name"
                  @returnFn="getMainCauseOfTreatment"
                ></Selecter>-->
                <MainCauseOfTreatmentList
                  @getSelect="getSelect"
                  v-show="this.$store.state.pData.option_set_key"
                ></MainCauseOfTreatmentList>
              </v-col>
              <v-col cols="12" md="3">
                <v-text-field
                  label="回診日期"
                  type="date"
                  v-model="list.back_date"
                ></v-text-field>
                <!-- <datepicker :label="'回診日期'" v-model="list.back_date" :rules="emptyRules"></datepicker> -->
              </v-col>
              <v-col cols="12" md="2">
                <v-select
                  :items="carItems"
                  label="使用車輛"
                  v-model="list.car"
                ></v-select>
              </v-col>
              <v-col cols="12" md="2">
                <v-select
                  :items="accompanyItems"
                  label="陪診人員"
                  v-model="list.accompany"
                ></v-select>
              </v-col>

              <v-col cols="12" md="3" class="d-flex">
                <v-combobox
                  :items="carryitemsItems"
                  multiple
                  label="攜帶物品"
                  v-model="list.carryArray"
                ></v-combobox>
              </v-col>

              <v-col cols="12">
                <v-alert color="primary" outlined>
                  <h4>使用中管路(名稱-到期日)</h4>
                  <v-chip
                    class="ma-2"
                    color="primary"
                    v-for="(plist, index) in matchPipelineItems"
                    :key="index"
                  >
                    {{ plist.name }} - {{ plist.end_date }}
                    <!-- <v-icon color="red" @click="delPipeline(index)">mdi-close</v-icon> -->
                  </v-chip>
                  <!-- <h4>管路資料設定</h4>
                  <v-row>
                    <v-col cols="12" md="6">
                      <v-autocomplete :items="pipelineItems" label="管路" v-model="pipelineArray.name" return-object></v-autocomplete>
                    </v-col>
                    <v-col cols="12" md="3">
                      <v-text-field label="管路到期日" type="date" v-model="pipelineArray.date"></v-text-field>
                    </v-col>
                    <v-col cols="12" md="3">
                      <v-btn class="primary" block @click.stop="addPipeline">新增管路</v-btn>
                    </v-col>
                    <v-col cols="12">
                      <h4>已設置管路(名稱-到期日)</h4>
                      <v-chip
                        class="ma-2"
                        color="primary"
                        v-for="(plist,index) in list.pipeline"
                        :key="index"
                      >
                        {{plist.name}} - {{plist.date}}
                        <v-icon color="red" @click="delPipeline(index)">mdi-close</v-icon>
                      </v-chip>
                    </v-col>
                  </v-row>-->
                </v-alert>
              </v-col>

              <v-col cols="12">
                <v-textarea
                  label="原因狀況描述"
                  v-model="list.description"
                ></v-textarea>
              </v-col>

              <v-col cols="12">
                <v-textarea
                  label="就醫情形回覆"
                  v-model="list.response"
                ></v-textarea>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-checkbox
            class="mb-3 mr-2 pa-2 error white-font"
            label="會計項目判斷"
            color="white"
            v-model="accountingCheck"
            value="true"
            hide-details
            v-if="processType == 'edit'"
          ></v-checkbox>
          <v-btn
            class="primary"
            @click="addOK"
            v-if="processType == 'add'"
            :loading="loading"
            :disabled="loading"
            >確認新增</v-btn
          >
          <v-btn
            class="success"
            @click="editOK"
            v-if="processType == 'edit'"
            :loading="loading"
            :disabled="loading"
            >確認修改</v-btn
          >
        </v-card-actions>
        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import datepicker from "@/components/DatePicker";
import HospitalList from "@/components/HospitalList";
import BranchList from "@/components/BranchList";
import DoctorList from "@/components/DoctorList";
import MainCauseOfTreatmentList from "@/components/MainCauseOfTreatmentList";
import dayjs from "dayjs";

// import Selecter from "@/components/Selecter";

export default {
  components: {
    datepicker,
    HospitalList,
    BranchList,
    DoctorList,
    MainCauseOfTreatmentList,
  },

  props: ["medicalRecordItems"],

  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
      accountingCheck: "", //修改時是否判斷會計車資項目

      timeItems: ["上午", "下午", "夜間"], //時段
      outpatientItems: ["門診", "急診", "拿藥", "巡診", "駐診"], //診別設定
      carItems: [
        "",
        "復康巴士(私人)",
        "復康巴士(公立)",
        "計程車",
        "救護車",
        "慈愛復健車",
        "家屬自載",
        "代拿藥",
        "家屬自取藥",
      ], //車輛設定
      accompanyItems: ["", "機構安排", "家屬自陪", "看護", "不需陪診"], //陪診設定
      pipelineItems: [
        "一般鼻胃管",
        "矽質鼻胃管",
        "一般尿管",
        "矽質尿管",
        "氣切",
        "其他",
      ],
      carryitemsItems: ["健保卡", "殘障手冊", "重大傷病卡", "TB手冊"],
      //存放新增前的管路資料
      pipelineArray: {
        name: "",
        date: "",
      },
      loading: false,
      hospitalItems: [],
      branchItems: [],
      doctorItems: [],
      main_cause_of_treatmentItems: [],

      matchPipelineItems: [], //使用中管路資料
    };
  },

  computed: {
    diagnosis() {
      if (this.$store.state.uData.diagnosis != undefined) {
        return Object.values(this.$store.state.uData.diagnosis);
      }
    },
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });

    this.getSelect();
  },

  methods: {
    //取得各項選單的資料
    getSelect() {
      this.getSelectItems("hospital");
      this.getSelectItems("branch");
      this.getSelectItems("doctor");
      this.getSelectItems("main_cause_of_treatment");
    },
    insertLast() {
      var lastData = this.medicalRecordItems.sort(function (a, b) {
        return a["date"] < b["date"] ? 1 : -1;
      });
      this.list = { ...lastData[0] };
      //處理各個select的內容
      var checkCol = [
        "hospital",
        "branch",
        "doctor",
        "main_cause_of_treatment",
      ];
      checkCol.forEach((i) => {
        this[i + "Items"].find((cItem) => {
          if (cItem.snkey == this.list[i + "_snkey"]) {
            this.$set(this.list, i + "data", cItem);
          }
        });
      });
      this.list.date = dayjs().format("YYYY-MM-DD");
    },
    mapData(database, data) {
      this[database + "Items"] = data.map((i) => {
        return {
          snkey: i.snkey,
          text: i.name,
        };
      });
    },
    //取得各項選單資料
    getSelectItems(database) {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getall/" + this.$store.state.databaseName + "/" + database
        )
        .then((rs) => {
          // this[database + "Items"] = rs.data;
          this.mapData(database, rs.data);
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    getPipeline() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey,
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/pipeline_used",
          qs.stringify(postData)
        )
        .then((rs) => {
          var pipelineData = rs.data.map((i) => {
            var newItem = JSON.parse(i.datalist);
            newItem["snkey"] = i.snkey;
            newItem["create_man"] = i.create_man;
            newItem["edit_man"] = i.edit_man;
            return newItem;
          });
          this.matchPipelineItems = pipelineData.filter((i) => {
            return (
              dayjs(i.end_date).format("YYYY-MM-DD") >
              dayjs().format("YYYY-MM-DD")
            );
          });

          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    //刪除管路資料
    delPipeline(index) {
      this.list.pipeline.splice(index, 1);
    },
    //新增管路資料
    addPipeline() {
      if (
        this.pipelineArray.name.length > 0 &&
        this.pipelineArray.date.length
      ) {
        this.list.pipeline.push({ ...this.pipelineArray });
      }
    },
    //進入畫面時,執行的是新增作業
    addProcess() {
      this.processType = "add";
      this.title = "新增醫院就診紀錄";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = {
        date: dayjs().format("YYYY-MM-DD"),
        pipeline: [],
        car: "",
        accompany: "",
      };
      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });
      this.getPipeline();
    },
    //進入畫面時,執行的是修改作業
    editProcess(item) {
      console.log(item);
      this.processType = "edit";
      this.title = "修改醫院就診紀錄";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //將頁面資料與父項的資料連結
      this.list = { ...item }; //複制一份實際修改資料內容
      // if (this.list.back_date == "0000-00-00") {
      //   this.list.back_date = "";
      // }

      //處理各個select的內容
      var checkCol = [
        "hospital",
        "branch",
        "doctor",
        "main_cause_of_treatment",
      ];
      checkCol.forEach((i) => {
        this[i + "Items"].find((cItem) => {
          if (cItem.snkey == this.list[i + "_snkey"]) {
            this.$set(this.list, i + "data", cItem);
          }
        });
      });

      // this.$set(this.list, "pipelineArray", this.list.pipeline.split(","));
      this.$set(this.list, "carryArray", this.list.carryitems.split(","));
      this.getPipeline();
    },
    addOK() {
      //判斷使用車輛
      if (this.list.car.includes("復康巴士") && this.list.accompany == "") {
        var pop = {
          msg: "使用復康巴需選擇陪診人員,請再確認",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
        return false;
      }
      //判斷使用車輛end

      //確認新增
      if (this.$refs.form.validate()) {
        //判斷未輸入資料
        var checkCol = [
          "hospital",
          "branch",
          "doctor",
          "main_cause_of_treatment",
        ];
        var noMatch = checkCol.filter((i) => {
          if (this.list[i + "data"] == undefined) {
            return i;
          } else {
            if (this.list[i + "data"].snkey == undefined) {
              return i;
            }
          }
        });
        if (noMatch.length > 0) {
          var pop = {
            msg: "資料輸入不完整或不符合規定,請再確認!!",
            type: true,
            theme: "warning",
          };
          this.$store.commit("snackbar", pop);
          return false;
        }
        //斷未輸入資料 end

        //建構資料
        this.$store.commit("getTime"); //取得現在時間
        var postData = {
          date: this.list.date,
          user_snkey: this.$store.state.uData.snkey,
          hospital_snkey: this.list.hospitaldata.snkey,
          branch_snkey: this.list.branchdata.snkey,
          doctor_snkey: this.list.doctordata.snkey,
          main_cause_of_treatment_snkey: this.list.main_cause_of_treatmentdata
            .snkey,
          time: this.list.time,
          outpatient: this.list.outpatient,
          description: this.list.description,
          response: this.list.response,
          back_date: this.list.back_date,
          car: this.list.car,
          accompany: this.list.accompany,
          create_man:
            this.$store.state.pData.username +
            "(" +
            this.$store.state.time +
            ")", //執行動作的人,時間,內容
        };

        if (this.list.pipeline != undefined) {
          postData["pipeline"] = JSON.stringify(this.list.pipeline);
        }
        if (this.list.carryArray != undefined) {
          postData["carryitems"] = this.list.carryArray.toString(); //處理combobox裡的資料 陣列->string
        }

        this.loading = true;
        //this.$store.commit("setLoadingOn"); //讀取畫面on
        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/medical_record",
            qs.stringify(postData)
          )
          .then((rs) => {
            console.log(rs.data);
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;
              this.$emit("getMedicalData");
              // this.$store.commit("setLoadingOff"); //讀取畫面off
              this.loading = false;
            }
          });

        //判斷會計帳
        this.accountingFeesCheck();
        // var carFee = { ...this.list }; //暫存判斷車資用
        // //會計帳建立->車資判斷 條件:醫院跟車種不可以是空白
        // if (carFee.car != undefined && carFee.hospitaldata != undefined) {
        //   console.log("run car");
        //   //this.$store.commit("setLoadingOn"); //讀取畫面on
        //   //取得所有資料
        //   this.axios
        //     .post("general/getAll/" + this.$store.state.databaseName + "/fee")
        //     .then((rs) => {
        //       // console.log(carFee);
        //       var feeData = [];
        //       if (carFee.car.includes("復康巴士(私人)")) {
        //         console.log("私人");
        //         if (
        //           this.list.main_cause_of_treatmentdata.text == "急診就醫" ||
        //           this.list.main_cause_of_treatmentdata.text == "出院帶藥"
        //         ) {
        //           feeData = rs.data.filter((i) => {
        //             return i.name.includes(
        //               carFee.hospitaldata.text + "(出、入院)"
        //             );
        //           });
        //         } else {
        //           feeData = rs.data.filter((i) => {
        //             return i.name.includes(carFee.hospitaldata.text + "(來回)");
        //           });
        //         }
        //         console.log(feeData);
        //       }

        //       if (
        //         carFee.car.includes("計程車") ||
        //         carFee.car.includes("救護車")
        //       ) {
        //         console.log(carFee.car);
        //         feeData = rs.data.filter((i) => {
        //           return i.name.includes(carFee.car);
        //         });
        //         console.log(feeData);
        //       }

        //       if (carFee.car.includes("代拿藥")) {
        //         console.log("代拿藥");
        //         feeData = rs.data.filter((i) => {
        //           return i.name.includes(carFee.car);
        //         });
        //         console.log(feeData);
        //       }

        //       //判斷是否有符合的車資 (>0 代表有符合的資料)
        //       if (feeData.length > 0) {
        //         var postData = {
        //           user_snkey: this.$store.state.uData.snkey,
        //           fee_snkey: feeData[0].snkey,
        //           date: carFee.date,
        //           start_day: carFee.date,
        //           end_day: carFee.date,
        //           quantity: 1,
        //           price: feeData[0].price,
        //           note: "(A)",
        //           create_man:
        //             this.$store.state.pData.username +
        //             "(" +
        //             this.$store.state.time +
        //             ")", //執行動作的人,時間,內容
        //         };

        //         this.axios
        //           .post(
        //             "general/add/" +
        //               this.$store.state.databaseName +
        //               "/accounting",
        //             qs.stringify(postData)
        //           )
        //           .then((rs) => {
        //             if (rs.data.state == 1) {
        //               var pop = {
        //                 msg: "就診紀錄已新增;[相關車資費用已新增]",
        //                 type: true,
        //                 theme: "success",
        //               };
        //               this.$store.commit("snackbar", pop);
        //               // this.$store.commit("setLoadingOff"); //讀取畫面off
        //             }
        //           });
        //       }
        //     });
        // }
      } else {
        var pop = {
          msg: "資料輸入不完整,請再確認",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    //確認修改
    editOK() {
      //判斷使用車輛
      if (this.list.car.includes("復康巴士") && this.list.accompany == "") {
        var pop = {
          msg: "使用復康巴需選擇陪診人員,請再確認",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
        return false;
      }
      //判斷使用車輛end

      if (this.$refs.form.validate()) {
        //判斷未輸入資料
        var checkCol = [
          "hospital",
          "branch",
          "doctor",
          "main_cause_of_treatment",
        ];
        var noMatch = checkCol.filter((i) => {
          if (this.list[i + "data"] == undefined) {
            return i;
          } else {
            if (this.list[i + "data"].snkey == undefined) {
              return i;
            }
          }
        });
        if (noMatch.length > 0) {
          var pop = {
            msg: "資料輸入不完整或不符合規定,請再確認!!",
            type: true,
            theme: "warning",
          };
          this.$store.commit("snackbar", pop);
          return false;
        }
        //斷未輸入資料 end

        // var postData = { ...this.list };
        // postData["user_snkey"] = this.$store.state.uData.snkey;
        // postData["pipeline"] = JSON.stringify(postData.pipeline);
        // postData["carryitems"] = postData.carryArray.toString(); //處理combobox裡的資料 陣列->string
        // // delete postData["pipelineArray"];
        // delete postData["carryArray"];
        // delete postData["hospital_name"];
        // delete postData["branch_name"];
        // delete postData["doctor_name"];
        // delete postData["main_cause_of_treatment_name"];
        // delete postData["branch"];

        // this.$store.commit("getTime"); //取得現在時間
        // postData["edit_man"] =
        //   this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        //建構資料
        this.$store.commit("getTime"); //取得現在時間
        var postData = {
          snkey: this.list.snkey,
          date: this.list.date,
          user_snkey: this.$store.state.uData.snkey,
          hospital_snkey: this.list.hospitaldata.snkey,
          branch_snkey: this.list.branchdata.snkey,
          doctor_snkey: this.list.doctordata.snkey,
          main_cause_of_treatment_snkey: this.list.main_cause_of_treatmentdata
            .snkey,
          time: this.list.time,
          outpatient: this.list.outpatient,
          description: this.list.description,
          response: this.list.response,
          back_date: this.list.back_date,
          car: this.list.car,
          accompany: this.list.accompany,
          edit_man:
            this.$store.state.pData.username +
            "(" +
            this.$store.state.time +
            ")", //執行動作的人,時間,內容
        };

        if (this.list.pipeline != undefined) {
          postData["pipeline"] = JSON.stringify(this.list.pipeline);
        }
        if (this.list.carryArray != undefined) {
          postData["carryitems"] = this.list.carryArray.toString(); //處理combobox裡的資料 陣列->string
        }
        //建構資料end

        this.loading = true;
        this.axios
          .post(
            "general/edit/" +
              this.$store.state.databaseName +
              "/medical_record",
            qs.stringify(postData)
          )
          .then((rs) => {
            console.log(rs.data);
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;
              this.$emit("getMedicalData");
              this.loading = false;
            }
          });

        //判斷會計帳 -> 例:車資
        if (this.accountingCheck) {
          // alert("run");
          this.accountingFeesCheck();
        }
        //判斷車資的會計帳end
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    //判斷會計帳-> 例:車資
    accountingFeesCheck() {
      var carFee = { ...this.list }; //暫存判斷車資用
      //會計帳建立->車資判斷 條件:醫院跟車種不可以是空白
      if (carFee.car != undefined && carFee.hospitaldata != undefined) {
        console.log("run car");
        //this.$store.commit("setLoadingOn"); //讀取畫面on
        //取得所有資料
        this.axios
          .post("general/getAll/" + this.$store.state.databaseName + "/fee")
          .then((rs) => {
            // console.log(carFee);
            var feeData = [];
            if (carFee.car.includes("復康巴士(私人)")) {
              console.log("私人");
              if (
                this.list.main_cause_of_treatmentdata.text == "急診就醫" ||
                this.list.main_cause_of_treatmentdata.text == "出院帶藥"
              ) {
                feeData = rs.data.filter((i) => {
                  return i.name.includes(
                    carFee.hospitaldata.text + "(出、入院)"
                  );
                });
              } else {
                feeData = rs.data.filter((i) => {
                  return i.name.includes(carFee.hospitaldata.text + "(來回)");
                });
              }
              console.log(feeData);
            }

            if (
              carFee.car.includes("計程車") ||
              carFee.car.includes("救護車")
            ) {
              console.log(carFee.car);
              feeData = rs.data.filter((i) => {
                return i.name.includes(carFee.car);
              });
              console.log(feeData);
            }

            //代拿藥判斷
            if (carFee.car.includes("代拿藥")) {
              feeData = rs.data.filter((i) => {
                return i.name.includes(carFee.car);
              });
              console.log("代拿藥",feeData);
            }

            //陪診費判斷
            if (carFee.accompany.includes("機構安排")){
              const matchRs = rs.data.find((i) => {
                return i.name.includes("陪診費");
              });
              feeData.push(matchRs);
              console.log("陪診費-機構安排",feeData);
            }

            //判斷是否有符合的車資 (>0 代表有符合的資料)
            if (feeData.length) {
              let postData = [];
              feeData.forEach(feeContent=>{
                let pushData = {
                  user_snkey: this.$store.state.uData.snkey,
                  fee_snkey: feeContent.snkey,
                  date: carFee.date,
                  start_day: carFee.date,
                  end_day: carFee.date,
                  quantity: 1,
                  price: feeContent.price,
                  note: "(A)",
                  create_man: this.$store.state.pData.username + "(" + this.$store.state.time + ")", //執行動作的人,時間,內容  
                  };
                   /* 20201106判斷日期是否超過26日 */
                  if (dayjs(pushData.date).format('DD') >= 26){
                    pushData.date = dayjs(pushData.date).add(1,'month').format('YYYY-MM-01');
                  }
                  /* 20201106判斷日期是否超過26日 end*/  

                  postData.push(pushData);
              })
              
              // postData = {
              //   user_snkey: this.$store.state.uData.snkey,
              //   fee_snkey: feeData[0].snkey,
              //   date: carFee.date,
              //   start_day: carFee.date,
              //   end_day: carFee.date,
              //   quantity: 1,
              //   price: feeData[0].price,
              //   note: "(A)",
              //   create_man:
              //     this.$store.state.pData.username +
              //     "(" +
              //     this.$store.state.time +
              //     ")", //執行動作的人,時間,內容
              // };

               /* 20201106判斷日期是否超過26日 */
                // if (dayjs(postData.date).format('DD') >= 26){
                //   postData.date = dayjs(postData.date).add(1,'month').format('YYYY-MM-01');
                // }
                /* 20201106判斷日期是否超過26日 end*/

              this.axios
                .post(
                  "general/addMulti/" +
                    this.$store.state.databaseName +
                    "/accounting",
                  qs.stringify(postData)
                )
                .then((rs) => {
                  if (rs.data.state == 1) {
                    var pop = {
                      msg: "就診紀錄已新增;[相關費用已新增]",
                      type: true,
                      theme: "success",
                    };
                    this.$store.commit("snackbar", pop);
                    // this.$store.commit("setLoadingOff"); //讀取畫面off
                  }
                });
            }
          });
      }
    },
  },
};
</script>

