<template>
  <v-combobox
    :label="label"
    :items="this.$store.getters[dataSource]"
    :rules="emptyRules"
    v-model="choiceItem"
    @change="choice"
  ></v-combobox>
</template>

<script>
export default {
  props: [ "dataSource" , "label" , "defaultData"],

  data: () => ({
    emptyRules: [v => !!v || "不可空白"],
    // items:[],
    choiceItem:"",
  }),

  watch:{
    defaultData(){
      // console.log('defaultData');
      this.choiceItem = this.defaultData;
    }
  },

  mounted() {
    // console.log(this.defaultData)
    this.$store.dispatch('getItems',this.dataSource);
    this.choiceItem = this.defaultData;  //設定預設值,不然不能觸發watch
  },

  methods: {
    choice(){
      if (this.choiceItem != null){
        // this.$emit("returnFn",this.choiceItem.snkey);
        this.$emit("returnFn",this.choiceItem);
      }
    }
  },

  
};
</script>